	module.exports = {"data":{"post":{"id":"/Users/georgiabarrie/gatsby-afc-guide/content/posts/2017-12-07--preventing-mastitus/index.md absPath of file >>> MarkdownRemark","html":"<blockquote style=\"background: #f2f3f4;\">\n<p style=\"text-align: center; font-weight: 300; font-size: 0.9em\">To add your question press <img style=\"height:2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/comment_icon.png\">  in bottom corner &#x1F447;</p>\n</blockquote>\n<div style=\"text-align: center\">\n<a href=\"/about\"><img style=\"width: 100%;\" src=\"https://s3.amazonaws.com/afc-dairytrial/key_bar-1.png\"></a>\n</div>\n<br><br>\nMastitis is one of the most common problems experienced by dairy farmers. Mastitis is an inflammation of the udder and is normally caused by bacteria getting inside the teat. \n<br><br>\nIn minor cases mastitis will reduce the litres of milk produced and result in poor quality milk. In very serious cases mastitis can cause cow death.\n<blockquote style=\"background: #D7F19F;\">\n<h3>What causes mastitis?</h3>\n<p>Mastitis is normally caused by poor milking practices or cows being housed in unclean environments. If your cow has not been fed properly or is stressed then she will be more susceptible to mastitis as she will find it harder to fight the infection.</p>\n<br>\n<div>\n    Source:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/ILRI-logo.png\">\n    <b>ILRI</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>21</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<p>Check out our 7 steps to preventing mastitis when milking…</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/afc_dairy_guides_mastitis_milking-8f0fea31e5dd075c30f4bea14cea9fd6-7af05.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 113.76936316695353%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"unsplash.com\"\n        title=\"\"\n        src=\"/static/afc_dairy_guides_mastitis_milking-8f0fea31e5dd075c30f4bea14cea9fd6-48538.png\"\n        srcset=\"/static/afc_dairy_guides_mastitis_milking-8f0fea31e5dd075c30f4bea14cea9fd6-bed0f.png 200w,\n/static/afc_dairy_guides_mastitis_milking-8f0fea31e5dd075c30f4bea14cea9fd6-5fd34.png 400w,\n/static/afc_dairy_guides_mastitis_milking-8f0fea31e5dd075c30f4bea14cea9fd6-48538.png 800w,\n/static/afc_dairy_guides_mastitis_milking-8f0fea31e5dd075c30f4bea14cea9fd6-7af05.png 1162w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Check out our 6 steps to preventing mastitis from the cowshed…</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/afc_dairy_guides_mastitis_cowshed-0ce8dbeffe6753bd409863827fa03396-7af05.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 84.76764199655766%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"unsplash.com\"\n        title=\"\"\n        src=\"/static/afc_dairy_guides_mastitis_cowshed-0ce8dbeffe6753bd409863827fa03396-48538.png\"\n        srcset=\"/static/afc_dairy_guides_mastitis_cowshed-0ce8dbeffe6753bd409863827fa03396-bed0f.png 200w,\n/static/afc_dairy_guides_mastitis_cowshed-0ce8dbeffe6753bd409863827fa03396-5fd34.png 400w,\n/static/afc_dairy_guides_mastitis_cowshed-0ce8dbeffe6753bd409863827fa03396-48538.png 800w,\n/static/afc_dairy_guides_mastitis_cowshed-0ce8dbeffe6753bd409863827fa03396-7af05.png 1162w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Top Q&#x26;A from AFC members</h3>\n<blockquote style=\"background: #6EA6A2;\">\n<h3>Wycliffe’s Question</h3>\n<p>  Hi farmers. How do we test for mastitis?!</p>\n  <br>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_mastitis_wycliffe.png\">\n    <b>Wycliffe O</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>12</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<p>Our AFC opinion on this topic</p>\n<blockquote style=\"background: #D7F19F;\">\n<h3>AFC opinion</h3>\n<p>Signs of mastitis include the udder looking swollen, hard or red. Before you milk the cow you should check the milk from each teat to make sure it is not watery or with clots/flakes/pus. These are signs of mastitis.\n<br></br>\nSome farmers regularly test their cows using the California Mastitis Test (CMT) which you can buy from your local agrovet. This is a good idea if you multiple cows and you think they may be at risk.\n<br></br></p>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_logo.png\">\n    <b>AFC</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>31</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<blockquote style=\"background: #6EA6A2;\">\n<h3>Beatrice’s Question</h3>\n<p>  Can someone advise me on how to treat mastitis on dairy cow?</p>\n  <br>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_mastitis_betty.png\">\n    <b>Beatrice K</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>12</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<p>Our AFC opinion on this topic</p>\n<blockquote style=\"background: #D7F19F;\">\n<h3>AFC opinion</h3>\n<p>If your cow has mastitis it is really important that you get her checked by a vet as the type of treatment will depend on the type of infection. It’s impossible to know what type of infection she has without getting tested.\n<br></br>\nOne way to help your cow recover is by milking regularly. Empty the infected teat every 3 hours and your cow will recover more quickly.\n<br></br></p>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_logo.png\">\n    <b>AFC</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>31</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<h3>Real Farmer Stories</h3>\n<p>Dan’s experience with Milk fever…</p>\n<blockquote style=\"background: #96e58e;\">\n<div>\n  <img style=\"width: 100%\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_mastitis_story.jpg\">\n  </div>\n<br>\n<p>  “DARKEST day in dairy farming is when one of your best dam, just after patiently waiting for it to calf, and you are expecting over 30 litres production, disaster structs. She is attacked by a severe case of mastitis and succumbs less than 72 hours after giving birth! Luckily you remain with a beautiful 46kg female calf to remember her with eventhough you even don’t have colostrum to feed her with. The kipsigis people used to say that “one should not prepare a calabash for storing milk before the cow calfs” Anything can and will happen. Am experiencing a worst sense of loss in my entire dairy farming life.”</p>\n<br>\n  <a href=\"https://www.facebook.com/groups/DairyFarmingKenya/permalink/1472254976170779/\">\n    <img style=\"height:2em; border-radius: 2em;border: 1px solid #ffff;\" src=\"https://s3.amazonaws.com/afc-dairytrial/read_full_story.png\">\n  </a>\n  <br><br>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_mastitis_dan.png\">\n    <b>Dan K</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>49</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<p>Got more questions about mastitis? Add your questions in the comments section below…</p>\n<hr>\n<h3>Read more topics</h3>\n<p>There are number of other important topics in this guide:</p>\n<ul>\n<li><a href=\"/feeding-your-cow-basics\">Feeding your cow - Basics</a> ⭐ - How to get started with feeding</li>\n<li><a href=\"/preventing-and-managing-bloat\">Preventing and Managing Bloat in Cows</a>  ⭐- Know what to look out for</li>\n</ul>\n<p>…or you can just <a href=\"/\">see all topics</a></p>","fields":{"slug":"/preventing-mastitus/","prefix":"2017-12-07"},"frontmatter":{"title":"Preventing Mastitis","subTitle":"Disease & animal health","cover":{"childImageSharp":{"resize":{"src":"/static/unlock-preventing-mastitus-5ec5318546520837e7b047609d969f30-160fa.png"}}}}},"author":{"id":"/Users/georgiabarrie/gatsby-afc-guide/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Africa Farmers Club</strong> has a mission to bring African farmers together!</p>"},"footnote":{"id":"/Users/georgiabarrie/gatsby-afc-guide/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<p>Made with ♥️ by Africa Farmers Club</p>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/preventing-mastitus/"}}

